<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-dialogs --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vDialogs from 'v-dialogs';
            Vue.use(vDialogs, { global config options... });
        </pre>

        <div class="panel panel-default">
            <div class="panel-body">
                <p><i18n path="common.globalConfig"></i18n></p>
                <ul>
                    <li>instanceName<br><i18n path="dialog.demo.instanceName"></i18n></li>
                    <li>language</li>
                    <li>dialogCloseButton</li>
                    <li>dialogMaxButton</li>
                </ul>
            </div>
        </div>

        <br>

        <h2 class="page-header"><i18n path="dialog.iconTitle"></i18n></h2>

        <p><i18n path="dialog.alertIcon"></i18n> <a href="http://www.elegantthemes.com/blog/freebie-of-the-week/beautiful-flat-icons-for-free" target="_blank">Elegant Themes</a></p>
        <p><i18n path="dialog.toastIcon"></i18n> <a href="http://www.iconfont.cn" target="_blank">IconFont</a></p>


        <br><br>

        <h2 class="page-header"><i18n path="common.example"></i18n></h2>
        <h3>Modal <small><i18n path="dialog.demo.modalTitle"></i18n></small></h3>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js;gutter: false;">
                this.$dlg.modal(component, [options]);
                </pre>

                <p>
                    <button type="button" class="btn btn-default" @click="callModal"><i class="fa fa-fw fa-window-maximize"></i> Open Modal</button>
                </p>

                <showcode>
                    <pre class="brush:js;gutter: false;">
                        import myComponent from './myComponent.vue';
                        export default {
                            methods: {
                                open(){
                                    this.$dlg.modal(myComponent, {
                                        width: 350,
                                        height: 450,
                                        //send params to inner component
                                        params: {
                                            name: 'Terry Zeng'
                                        },
                                        //modal dialog close callback
                                        //data: inner component call 'this.$emit.('close', data)'
                                        //to close modal and return data
                                        callback: data=>{
                                            //alert selected result
                                            this.$dlg.alert(`You selected ${data.companyName} company`);
                                        }
                                    });
                                }
                            }
                        };
                    </pre>
                </showcode>
            </div>
        </div>


        <br>
        <h3>Alert <small><i18n path="dialog.demo.alertTitle"></i18n></small></h3>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js;gutter: false;">
                this.$dlg.alert(message, [callback], [options]);
                </pre>
                <div class="row">
                    <p class="col-md-3">
                        <select v-model="alertType" class="form-control">
                            <option value="info">info</option>
                            <option value="warning">warning</option>
                            <option value="error">error</option>
                            <option value="success">success</option>
                            <option value="confirm">confirm</option>
                        </select>
                    </p>
                </div>

                <p>
                    <button type="button" class="btn btn-default" @click="callAlert"><i class="fa fa-fw fa-warning"></i> Open Alert</button>
                </p>

                <showcode>
                    <pre class="brush:js;gutter: false;">
                        //show message in alert dialog, using information type by default
                        this.$dlg.alert('Data saved successfully!');

                        //show message with close callback
                        this.$dlg.alert('Data saved successfully!', function(){
                            //dialog close callback
                        });

                        //show message with options
                        this.$dlg.alert('Data saved successfully!', {
                            messageType: 'success',
                            language: 'en'
                        });

                        //show message with close callback and options
                        this.$dlg.alert('Data saved successfully!', function(){
                            //dialog close callback
                        }, {
                            messageType: 'success',
                            language: 'en'
                        });

                        //confirm type alert dialog will have one more button(cancel)
                        this.$dlg.alert('Do you really want to leave?', {
                            messageType: 'confirm',
                            cancelCallback: function(){
                                //do something when you click cancel button
                            }
                        });
                    </pre>
                </showcode>

            </div>
        </div>


        <br>
        <h3>Mask <small><i18n path="dialog.demo.maskTitle"></i18n></small></h3>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js;gutter: false;">
                this.$dlg.modal(message, [callback], [options]);
                </pre>

                <p>
                    <button type="button" class="btn btn-default" @click="callMask"><i class="fa fa-fw fa-object-ungroup"></i> Open Mask</button>
                </p>


                <showcode>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.mask('form data saving', function(){
                            //mask close callback
                        });
                    </pre>
                    <h4>practical use case for Mask</h4>
                    <pre class="brush:js;gutter: false;">
                        export default {
                            methods: {
                                save(){
                                    //open a full screen mask
                                    //using default text: 'Loading...'
                                    const key = this.$dlg.mask();
                                    //do some request from server side
                                    axios({...}).then(resp=>{
                                        //do your stuff
                                        ...
                                        //job done, close mask dialog by key
                                        this.$dlg.close(key);
                                    });
                                }
                            }
                        }
                    </pre>
                </showcode>
            </div>
        </div>


        <br>
        <h3>Toast <small><i18n path="dialog.demo.toastTitle"></i18n></small></h3>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js;gutter: false;">
                this.$dlg.toast(message, [callback], [options]);
                </pre>
                <div class="row">
                    <p class="col-md-3">
                        <select v-model="toastType" class="form-control">
                            <option value="info">info</option>
                            <option value="warning">warning</option>
                            <option value="error">error</option>
                            <option value="success">success</option>
                        </select>
                    </p>

                    <p class="col-md-3">
                        <select v-model="position" class="form-control">
                            <option value="topLeft">topLeft</option>
                            <option value="topCenter">topCenter</option>
                            <option value="topRight">topRight</option>
                            <option value="bottomLeft">bottomLeft</option>
                            <option value="bottomCenter">bottomCenter</option>
                            <option value="bottomRight">bottomRight</option>
                        </select>
                    </p>

                </div>



                <p>
                    <button type="button" class="btn btn-default" @click="callToast"><i class="fa fa-fw fa-paper-plane-o"></i> Open Toast</button>
                </p>


                <showcode>
                    <pre class="brush:js;gutter: false;">
                        //show message in toast dialog, using information type and show in bottom right corner by default
                        this.$dlg.toast('you got a new message!');

                        //show message with close callback
                        this.$dlg.toast('you got a new message!', function(){
                            //toast close callback
                        });

                        //show message with options
                        this.$dlg.toast('you got a new message!', {
                            messageType: 'warning',
                            closeTime: 3 //auto close dialog time(second)
                        });

                        //show message with close callback and options
                        this.$dlg.toast('you got a new message!', function(){
                            //toast close callback
                        }, {
                            messageType: 'warning',
                            closeTime: 3 //auto close dialog time(second)
                        });
                    </pre>
                </showcode>
            </div>
        </div>

        <br><br>

        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped option-table">
            <thead>
                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                    <th><i18n path="common.tbTitle.scope"></i18n></th>
                </tr>
            </thead>
            <tbody>
            <!--<tr>-->
                <!--<td>message</td>-->
                <!--<td>string</td>-->
                <!--<td><i18n path="dialog.option.message"></i18n></td>-->
                <!--<td>undefined</td>-->
                <!--<td>Alert, Mask, Toast</td>-->
            <!--</tr>-->
            <tr>
                <td>messageType</td>
                <td>string</td>
                <td>
                    <i18n path="dialog.option.messageType"></i18n>
                    <ul>
                        <li><code>'info'</code></li>
                        <li><code>'warning'</code></li>
                        <li><code>'error'</code></li>
                        <li><code>'success'</code></li>
                        <li><code>'confirm'</code> only work on Alert mode</li>
                    </ul>
                </td>
                <td>'info'</td>
                <td>Alert, Toast</td>
            </tr>
            <tr>
                <td>title</td>
                <td>string | boolean</td>
                <td><i18n path="dialog.option.title"></i18n></td>
                <td>'Dialog'</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>width</td>
                <td>number</td>
                <td><i18n path="dialog.option.width"></i18n></td>
                <td>700</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>height</td>
                <td>number</td>
                <td><i18n path="dialog.option.height"></i18n></td>
                <td>400</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>params</td>
                <td>object</td>
                <td><i18n path="dialog.option.params"></i18n></td>
                <td>undefined</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>dialogCloseButton</td>
                <td>boolean</td>
                <td><i18n path="dialog.option.dialogCloseButton"></i18n></td>
                <td>true</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>dialogMaxButton</td>
                <td>boolean</td>
                <td><i18n path="dialog.option.dialogMaxButton"></i18n></td>
                <td>true</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>position</td>
                <td>string</td>
                <td><i18n path="dialog.option.position"></i18n>
                    <ul>
                        <li><code>'topLeft'</code></li>
                        <li><code>'topCenter'</code></li>
                        <li><code>'topRight'</code></li>
                        <li><code>'bottomLeft'</code></li>
                        <li><code>'bottomCenter'</code></li>
                        <li><code>'bottomRight'</code></li>
                    </ul>
                </td>
                <td>'bottomRight'</td>
                <td>Toast</td>
            </tr>
            <tr>
                <td>singletonKey</td>
                <td>string</td>
                <td><i18n path="dialog.option.singletonKey"></i18n></td>
                <td>undefined</td>
                <td>All</td>
            </tr>
            <tr>
                <td>language</td>
                <td>string</td>
                <td><i18n path="dialog.option.language"></i18n>
                    <ul>
                        <li><code>'cn'</code> - Chinese</li>
                        <li><code>'en'</code> - English</li>
                        <li><code>'jp'</code> - Japanese</li>
                        <li><code>'pt'</code> - Portuguese</li>
                    </ul>
                </td>
                <td>'cn'</td>
                <td>All</td>
            </tr>
            <tr>
                <td>closeTime</td>
                <td>number | boolean</td>
                <td><i18n path="dialog.option.closeTime"></i18n></td>
                <td>false</td>
                <td>Alert,Mask,Toast</td>
            </tr>
            <tr>
                <td>cancelCallback</td>
                <td>number | boolean</td>
                <td><i18n path="dialog.option.cancelCallback"></i18n></td>
                <td>false</td>
                <td>Alert</td>
            </tr>
            </tbody>
        </table>



        <h2 class="page-header">API</h2>
        <table class="table table-bordered">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.apiName"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>modal</td>
                <td>
                    <p><i18n path="dialog.demo.modalTitle"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.modal(component, [options]);
                    </pre>
                </td>
            </tr>
            <tr>
                <td>alert</td>
                <td>
                    <p><i18n path="dialog.demo.alertTitle"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.alert(message, [callback], [options]);
                    </pre>
                </td>
            </tr>
            <tr>
                <td>mask</td>
                <td>
                    <p><i18n path="dialog.demo.maskTitle"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.mask(message, [callback], [options]);
                    </pre>
                </td>
            </tr>
            <tr>
                <td>toast</td>
                <td>
                    <p><i18n path="dialog.demo.toastTitle"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.toast(message, [callback], [options]);
                    </pre>
                </td>
            </tr>
            <tr>
                <td>close</td>
                <td>
                    <p><i18n path="dialog.api.close"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        //open mask and get dialog key
                        const key = this.$dlg.mask();
                        //close dialog with key
                        this.$dlg.close(key);
                    </pre>
                </td>
            </tr>
            <tr>
                <td>closeAll</td>
                <td>
                    <p><i18n path="dialog.api.closeAll"></i18n></p>
                    <pre class="brush:js;gutter: false;">
                        this.$dlg.closeAll(function(){
                            //do something after all dialog closed
                        });
                    </pre>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import profile from './UserProfile';
    export default {
        name: "Demo",
        data(){
            return {
                alertType: 'info',
                position: 'bottomRight',
                toastType: 'info'
            }
        },
        methods:{
            callModal(){
                let that = this;
                this.$dlg.modal(profile, {
                    width: 350,
                    height: 450,
                    title: 'User profile',
                    callback: function(data){
                        that.$dlg.alert(`The user company is: <b>${data.companyName?data.companyName:'Not Selected'}</b>`);
                    },
                    params: {
                        name: 'Terry Zeng'
                    }
                });
            },
            callAlert(){
            	const option = {
					messageType: this.alertType
				};
            	if(this.alertType === 'confirm'){
            		option.cancelCallback = ()=>{
            			this.$dlg.alert('You canceled confirm dialog!');
                    };
                }
                this.$dlg.alert(`This is a Alert dialog, messageType is ${this.alertType}`, option);
            },
            callMask(){
                this.$dlg.mask(null,{
                    closeTime: 3
                });
            },
            callToast(){
                let that = this;
                this.$dlg.toast(`This is a Toast dialog, messageType is ${this.toastType}`, {
                    messageType: that.toastType,
                    closeTime: 3,
                    position: that.position
                })
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style scoped>

</style>


